.login-container {
    margin-top: 120px;
    z-index: 0;

    .google-login {
        background-color: rgba(241, 252, 255, 0.548);
        display: flex;
        flex-direction: column;
        align-items: center;
        margin:20px 40px;
        border-radius: 20px;
        padding:30px 0;
        img {
            width: 40%;
        }

        button{
            border-radius:5px;
        }
    }

}

.google-logout {
    z-index: 0;
    display: flex;
    justify-content:center;
}