$textcolor: #325080;
$bgacolor: #f4511e;
$bodercolor: #b7b7b6;

.cart-container {
    margin-top: 120px;
    z-index: 0;
    margin-bottom: 30px;

    .wide {

        .cart-title {

            .quality,
            .total {
                display: flex;
                align-items: center;
                align-content: center;
                justify-content: flex-start;
            }

            .total {
                justify-content: flex-end
            }

            h1 {
                font-weight: 300;
                font-size: 40px;
            }
        }

        .cart-main {
            border-top: 1px solid $bodercolor;
            border-bottom: 1px solid $bodercolor;
            padding: 10px;
            padding-top: 20px;
            transition-duration: 0.5s;
            .cart-img {
                img {
                    width: 100%;
                }
            }

            .cart-content {
                h3 {
                    margin-top: 0;
                    margin-bottom: 6px;
                }

                p {
                    font-size: 18px;
                    font-weight: 300;
                    -webkit-line-clamp: 3;
                    -webkit-box-orient: vertical;
                    display: -webkit-box;
                    overflow: hidden;
                    margin-top: 0;
                }
            }

            .cart-price {
                p {
                    margin: 0;
                }
            }

            .cart-input-quality {
                p {
                    margin: 0;
                    width: 40px;
                    z-index: 1;
                    background-color: #fff;
                    text-align: center;
                }
            }

            .cart-delete-total {
                span {
                    float: right;
                }

                button {
                    background-color: red;
                    border: none;
                    color: #fff;
                    padding: 3px 10px;
                    border-radius: 6px;
                }
            }
        }

        .cart {
            background-color: rgba(241, 252, 255, 0.548);
            overflow: hidden;
            padding: 10px;
            border-radius: 20px;
            a{
                color: rgb(36, 16, 255)
            }
            >p{
                text-align: center;
                font-size: 18px;
            }
        }

        .cart-total {
            display: flex;
            width: 100%;
            flex-direction: column;
            margin-bottom: 20px;

            .col {
                display: flex;
                margin-left: auto;
                width: 50%;

                .total-left {}

                .total-right {
                    margin-left: auto;
                }

            }

            button {
                width: 150px;
                margin-left: auto;
                padding: 10px;
                color: #fff;
                background-color: $textcolor;
                font-size: 20px;
                border-radius: 6px;
            }
        }
    }
}

@media (max-width:46.18em) {

    .quality,
    .total {
        display: none !important;
    }
}